import React, { useEffect, useState } from "react";

const CardList = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [users, setUsers] = useState("");
  const [searchfield, setSearchfield] = useState("");

  useEffect(() => {
    setIsLoading(true);
    const apiURL = `https://jsonplaceholder.typicode.com/users`;
    fetch(apiURL)
      .then((response) => response.json())
      .then((data) => {
        setUsers(data);
      });
  }, [setUsers]);

  console.log(users);

  return (
    <>
      <div>
        <div className="flex items-center justify-center"></div>
        <div className="flex justify-center min-h-screen">
          <div className="flex-1 max-w-5xl p-16">
            <div className="grid grid-cols-4 grid-rows-3 gap-4 grid-flow-row">
              {users
                ? users.map((user) => (
                    <div
                      key={user.id}
                      className="p-4 pr-6 bg-headline border-1-8 border-transparent rounded-md shadow-md space-y-2 transition duration-500 ease-in-out bg-main hover:bg-secondary transform hover:-translate-y-1 hover:scale-110"
                    >
                      <img
                        alt="monster"
                        src={`https://robohash.org/${user.id}?set=set3?size=180x180`}
                      />
                      <h1 className="text-lg font-semibold leading-6 justify-self-center">
                        {user.name}
                      </h1>
                      <p className="text-lg justify-self-center">
                        {user.email}
                      </p>
                    </div>
                  ))
                : "loading"}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default CardList;
